import { css, modifier, styled, variants } from '@lokalise/styled';

import { spacing, value } from '../../utils/theme';
import { Pressable } from '../Pressable';
import type { Appearance } from './IconButton';

type IconButtonProps = {
	appearance: Appearance;
	active?: boolean;
	disabled?: boolean;
	isLoading: boolean;
};

const variantColorStyles = css`
	${variants('appearance', {
		primary: css`
			padding: 6px;
			font-size: 20px;
			color: ${value('color.text.default')};

			// hover
			&:hover:not([disabled]) {
				background-color: ${value('palette.blue.100')};
				color: ${value('color.background.action.primary.hover')};
			}

			// pressed
			&:active:not([disabled]),
			&:focus:active:not([disabled]) {
				background-color: ${value('color.background.action.primary.hover')};
				color: ${value('color.text.inverse')};
			}

			// focus
			&:focus-visible:not([disabled]) {
				background-color: ${value('palette.blue.100')};
				color: ${value('color.background.action.primary.hover')};
			}

			// activated
			${modifier(
				'active',
				css`
					background-color: ${value('palette.neutral.100')};
					color: ${value('color.background.action.primary.hover')};
				`,
			)}
		`,
		secondary: css`
			padding: ${spacing(1)};
			font-size: 16px;
			color: ${value('color.text.default')};

			// hover
			&:hover:not([disabled]) {
				background-color: ${value('color.border.light')};
				color: ${value('color.text.default')};
			}

			// pressed
			&:active:not([disabled]),
			&:focus:active:not([disabled]) {
				background-color: ${value('color.border.default')};
				color: ${value('color.text.default')};
			}

			// focus
			&:focus-visible:not([disabled]) {
				background-color: ${value('color.border.light')};
				color: ${value('color.text.default')};
			}

			// activated
			${modifier(
				'active',
				css`
					background-color: ${value('color.background.surface.middle')};
					color: ${value('color.text.default')};
				`,
			)}
		`,
		verified: css`
			padding: ${spacing(1)};
			font-size: 16px;
			color: ${value('color.text.default')};

			// hover
			&:hover:not([disabled]) {
				background-color: ${value('color.deprecated.surface.yellow100')};
				color: ${value('color.text.default')};
			}

			// pressed
			&:active:not([disabled]),
			&:focus:active:not([disabled]) {
				background-color: ${value('color.deprecated.yellow.200')};
				color: ${value('color.text.default')};
			}

			// focus
			&:focus-visible:not([disabled]) {
				background-color: ${value('color.deprecated.surface.yellow100')};
				color: ${value('color.text.default')};
			}

			// activated
			${modifier(
				'active',
				css`
					background-color: ${value('color.deprecated.surface.yellow50')};
					color: ${value('color.text.default')};
				`,
			)}
		`,
		reviewed: css`
			padding: ${spacing(1)};
			font-size: 16px;
			color: ${value('color.text.default')};

			// hover
			&:hover:not([disabled]) {
				background-color: ${value('color.deprecated.surface.green100')};
				color: ${value('color.text.default')};
			}

			// pressed
			&:active:not([disabled]),
			&:focus:active:not([disabled]) {
				background-color: ${value('color.deprecated.green.200')};
				color: ${value('color.text.default')};
			}

			// focus
			&:focus-visible:not([disabled]) {
				background-color: ${value('color.deprecated.surface.green100')};
				color: ${value('color.text.default')};
			}

			// activated
			${modifier(
				'active',
				css`
					background-color: ${value('color.deprecated.surface.green50')};
					color: ${value('color.text.default')};
				`,
			)}
		`,
	})}
`;

export const Container = styled(Pressable)<IconButtonProps>`
	display: inline-flex;
	align-items: center;
	background-color: transparent;
	transition: background-color ${value('transition.default')}, color ${value('transition.default')};
	border: 0;
	border-radius: ${value('radius.m')};
	text-decoration: none;

	${variantColorStyles}

	// disabled
    &:disabled {
		cursor: not-allowed;
		color: ${value('color.text.disabled')};

		${modifier(
			'isLoading',
			css`
				color: initial;
				cursor: auto;
				${variantColorStyles}
			`,
		)}
	}
`;
